<!--   翻译文本输入-->
<template>
	<div class="Input">
		
		
		<textarea  v-model='tex'  placeholder="Please input what you need !"></textarea>
	    <select v-model='languague'>
	
	  	<option value="zh"  >Chinese</option>
	  	<option value="en" >English</option>
	  	<option value="yue">Cantonese</option>
	  	<option value="jp">Japanese</option>
	   </select>
		<button v-on:click='translate(tex)'>summti</button>

	</div>
	
</template>


<script>
import md5 from 'js-md5'
import $ from 'jquery'
import bus from '../assets/bus';
	
	export default{
		name:'Input',
		data(){
			return{
			tex:'',
			result:'',
			languague:''
				
		}
	},
	
	methods:{
		 translate:function(tex,language){
			var appid = '20180802000191282';
			var key = 'RHGVkzAkotHOEHKQbvtp';
			var salt = (new Date).getTime();
			var respon='';
			var query = tex;
			var from = 'auto';
			var to = this.languague;
			var str1 = appid + query + salt +key;
			var sign = md5(str1);
			
			if(this.languague=="")
			alert("请在右侧下拉列表选择你要翻译的语言种类");
					$.ajax({
					    url: 'http://api.fanyi.baidu.com/api/trans/vip/translate',
					    type: 'get',
					    dataType: 'jsonp',
					    data: {
					        q: query,
					        appid: appid,
					        salt: salt,
					        from: from,
					        to: to,
					        sign: sign
					    },
					    
					    success: function (data) {
					        console.log(data);
					        respon=data;
//					       alert(respon.trans_result[0].dst);
					       this.result=respon.trans_result[0].dst;
					         
					      bus.$emit('sendResult',this.result)   
					    } 
					});
			
						
				}
				

	}
}
</script>

<style scoped>
	div{
		position: relative;
	}
	textarea {
		
		width: 400px;
		height: 150px;
		border-radius: 5px;
		position: relative;
		top: 80px;
		left: 115px;
		font-family: "微软雅黑";
	
	}
	
	button{
		
		position: absolute;
		border-radius: 5px;
		left: 532px;
		top: 200px;
		width: 100px;
	}
	
	select{
	    position: relative;
		width: 100px;
		height: 20px;
		border-radius: 5px;
		left: 120px;
	}
</style>


