<!--   翻译文本展示-->
<template>
	<div class="show">

		<p>翻译结果：{{result}}</p>
	</div>
	
</template>


<script>
import bus from '../assets/bus'
	
	export default{
		name:'show',
		data(){
			return{
		            result:'',
		
				
		}
	},
	
	mounted(){
		var self = this;
		bus.$on('sendResult',function(aa){
			self.result=aa;
		})
		
	}
	
	
	}

</script>

<style scoped>
	
	div{
		position: relative;
		left: auto;
		top: 80px;
		left: 120px;
		width: 400px;
		height: auto;
		
	}
</style>


