<template>
  <div id="app">
  	<h2>Online Translate</h2>
  <ul>
		<li v-for='single in singles'>
			{{single}}
		</li></ul>
		<br />
		<br />
     <Input></Input>
     <show></show>
  


  </div>
</template>

<script>
import Input from './components/Input'
import show from './components/show'

export default {
  name: 'App',
  
  data(){
	return{
		title:'first vue',
		singles:['简单','易用','便捷'],
	 }
  }
  ,
  components:{
  	Input,
  	show 
  }
}
</script>

<style scoped>
	#app{
		width: 720px;
		height:1080px;
		margin: 0 auto;
		border: dashed;
		background-color: lightgreen;
		
	}
h2{
	position: relative;
	display: block;
	top: 100px;
	left: 280px;
  
}	
ul{
	position: relative;
	width: 450px;
	height: 30px;
	border: dashed;
	margin: 0 auto;
	top: 100px;
	background-color: black;
}	
li{
	display: block;
	width: 100px;
	height: 30px;
	background-color: chartreuse;
	float: left;
	text-align: center;
	line-height: 30px;
	margin-right: 50px;
		
}
</style>



